import Vue from 'vue';
import Router from 'vue-router';
Vue.use(Router);
export default new Router({
  mode: 'history',
  routes: [{
    path: '/jdweb',
    name: 'Home',
    component: function component(resolve) {
      return require(['./components/Home/Home'], resolve);
    }
  }, {
    path: '/jdweb/doc',
    name: 'Doc',
    component: function component(resolve) {
      return require(['./components/Doc/Doc'], resolve);
    }
  }, {
    path: '/jdweb/tools/search',
    name: 'Search',
    component: function component(resolve) {
      return require(['./components/Tools/Search/Search'], resolve);
    }
  }, {
    path: '/jdweb/tools/contribution',
    name: 'Contribution',
    component: function component(resolve) {
      return require(['./components/Tools/Contribution/Contribution'], resolve);
    }
  }, {
    path: '/jdweb/tools/study',
    name: 'Study',
    component: function component(resolve) {
      return require(['./components/Tools/Study/Study'], resolve);
    }
  }]
});